  import urls from '../../utils/urls.js'
  export default {
    props: ['isShow'],
    data() {
      return {
      }
    },
    methods: {
      cancle () {
        this.$emit('goClose', false)
      },
      getUserInfo: function () {
        uni.login({
          provider: 'weixin',
          success: (res) => {
            console.log(res.code)
            var code = res.code
            uni.getUserInfo({
              provider: 'weixin',
              success: (userRes) => {
                // console.log(userRes)
                userRes.userInfo.code = code
                uni.request({
                  url: urls.HTTP + '/user/checkapplet',
                  method: 'POST',
                  data: JSON.stringify(userRes.userInfo),
                  success: (res) => {
                    console.log(res)
                    if (res.data.code === 1) {
                      uni.setStorageSync('openid', res.data.openid)
                      uni.redirectTo({
                        url: 'register'
                      })
                    } else if (res.data.code === 0) {
											uni.setStorageSync('openid', res.data.user.openid)
											uni.setStorageSync('user', res.data.user)
											uni.setStorage({
												key: 'userInfo',
												data: JSON.stringify(res.data.user), // 对象转字符串进行本地存储
												success: (res) => {
													console.log(res)
												}
											})
											this.$store.dispatch('isLogin', true)											
											uni.switchTab({
												url: '../find/find'
											})											
                    } else {
                      uni.showToast({
                        title: '登录失败，请检查您的网络',
                        icon: 'none'
                      })
                    }
                  }
                })
              }
            })
          }
        })
      }
    }
  }
